import config from "./WebIMConfig";
import websdk from "easemob-websdk";
import emedia from "easemob-emedia";
import webrtc from "easemob-webrtc";
import { Message } from "element-ui";
function ack(message){
	var bodyId = message.id; // id
	var msg = new WebIM.message("read", WebIM.conn.getUniqueId());
	msg.set({
		id: bodyId,
		to: message.from
	});
	WebIM.conn.send(msg.body);
}

// IM SDK
var WebIM = {};
WebIM = window.WebIM = websdk;
WebIM.config = config;
WebIM.conn = new WebIM.connection({
	appKey: WebIM.config.appkey,
	isHttpDNS: WebIM.config.isHttpDNS,
	isMultiLoginSessions: WebIM.config.isMultiLoginSessions,
	https: WebIM.config.https,
	url: WebIM.config.xmppURL,
	apiUrl: WebIM.config.apiURL,
	isAutoLogin: true,
	heartBeatWait: WebIM.config.heartBeatWait,
	autoReconnectNumMax: WebIM.config.autoReconnectNumMax,
	autoReconnectInterval: WebIM.config.autoReconnectInterval,
	isStropheLog: WebIM.config.isStropheLog,
	delivery: WebIM.config.delivery
});
if(!WebIM.conn.apiUrl){
	WebIM.conn.apiUrl = WebIM.config.apiURL;
}

// 
WebIM.conn.listen({
	onOpened: function(message){ // 
		//  
		const username = Vue.$store.state.login.username || localStorage.getItem("userInfo") && JSON.parse(localStorage.getItem("userInfo")).userId;
		const path = location.pathname.indexOf("login") !== -1 || location.pathname.indexOf("register") !== -1 ? "/contact" : location.pathname;
		const redirectUrl = `${path}?username=${username}`;
		console.log(redirectUrl + "path");
		Vue.$router.push({ path: redirectUrl });
	},
	onClosed: function(message){
		// console.log("312323232");
		// Vue.$router.push({ path: "/login" });
	}, // 
	onTextMessage: function(message){
		const { from, to, type } = message;
		const chatId = type !== "chat" ? to : from;
		const typeMap = {
			chat: "contact",
			groupchat: "group",
			chatroom: "chatroom"
		};
		Vue.$store.commit("updateMsgList", {
			chatType: typeMap[message.type],
			chatId: chatId,
			msg: message.data,
			bySelf: false,
			from: message.from,
			mid: message.id
		});
		type === "chat" && ack(message);
		if(WebIM && WebIM.call && message && message.ext && message.ext.msg_extension){
			var msgExtension = message.ext.msg_extension && JSON.parse(message.ext.msg_extension);
			var options = {
				confrId: message.ext.conferenceId,
				password: message.ext.password || "",
				gid: msgExtension.group_id,
				inviter: msgExtension.inviter
			};
			WebIM.call.listener.onInvite(message.from, options);
		}
	}, // 
	onEmojiMessage: function(message){
		console.log("onEmojiMessage", message);
		const { type } = message;
		type === "chat" && ack(message);
	}, // 
	onPictureMessage: function(message){
		const { from, to, type } = message;
		const chatId = type !== "chat" ? to : from;
		const typeMap = {
			chat: "contact",
			groupchat: "group",
			chatroom: "chatroom"
		};
		Vue.$store.commit("updateMsgList", {
			chatType: typeMap[message.type],
			chatId: chatId,
			msg: message.url,
			bySelf: false,
			type: "img",
			from: message.from
		});
		type === "chat" && ack(message);
	}, // 
	onCmdMessage: function(message){
		console.log("onCmdMessage", message);
	}, // 
	onAudioMessage: function(message){
		const typeMap = {
			chat: "contact",
			groupchat: "group",
			chatroom: "chatroom"
		};
		const chatId = message.type !== "chat" ? message.to : message.from;
		let options = {
			url: message.url,
			headers: { Accept: "audio/mp3" },
			onFileDownloadComplete: function(response){
				let objectUrl = WebIM.utils.parseDownloadResponse.call(WebIM.conn, response);
				Vue.$store.commit("updateMsgList", {
					chatType: typeMap[message.type],
					chatId: chatId,
					msg: objectUrl,
					bySelf: false,
					type: "audio",
					from: message.from
				});
			},
			onFileDownloadError: function(){
				console.log("");
			}
		};
		WebIM.utils.download.call(WebIM.conn, options);
		message.type === "chat" && 	ack(message);
	}, // 
	onLocationMessage: function(message){
		console.log("onLocationMessage", message);
		message.type === "chat" && 	ack(message);
	}, // 
	onFileMessage: function(message){
		const { from, to, type } = message;
		const chatId = type !== "chat" ? to : from;
		const typeMap = {
			chat: "contact",
			groupchat: "group",
			chatroom: "chatroom"
		};
		Vue.$store.commit("updateMsgList", {
			chatType: typeMap[message.type],
			chatId: chatId,
			msg: message.url,
			bySelf: false,
			type: "file",
			filename: message.filename,
			file_length: message.file_length,
			from: message.from
		});
		type === "chat" && 	ack(message);
	}, // 
	onVideoMessage: function(message){
		console.log("onVideoMessage", message);
		const { from, to, type } = message;
		const chatId = type !== "chat" ? to : from;
		const typeMap = {
			chat: "contact",
			groupchat: "group",
			chatroom: "chatroom"
		};
		let options = {
			url: message.url,
			headers: {
				Accept: "audio/mp4"
			},
			onFileDownloadComplete: function(response){
				let objectURL = WebIM.utils.parseDownloadResponse.call(WebIM.conn, response);
				Vue.$store.commit("updateMsgList", {
					chatType: typeMap[message.type],
					chatId: chatId,
					msg: objectURL,
					bySelf: false,
					type: "video",
					filename: message.filename,
					file_length: message.file_length,
					from: message.from
				});
			},
			onFileDownloadError: function(){
				console.log("");
			}
		};
		WebIM.utils.download.call(WebIM.conn, options);
		type === "chat" && 	ack(message);
	}, // 
	onPresence: function(message){
		console.log("onPresence", message);
		let select_id = Vue.$store.state.group.groupInfo.gid; // 
		switch(message.type){
		case "subscribe":
			let options = {
				isShow: true,
				...message
			};
			Vue.$store.commit("changeFriendRequestState", options);
			break;
		case "subscribed":
			Vue.$store.dispatch("onGetContactUserList");
			Message({
				type: "success",
				message: message.from + " " + ""
			});
			break;
		case "unsubscribed":
			Vue.$store.dispatch("onGetContactUserList");
			// TODO
			if("code" in message){
				alert(message.from + " " + "");
			}
			else{
				Message({
					type: "success",
					message: message.from + " " + ""
				});
			}
			break;

		case "direct_joined": // --
			Vue.$store.dispatch("onGetGroupUserList");
			Message({
				type: "success",
				message: `${message.from}${message.gid}`
			});
			break;
		case "invite": // 
			let groupInviteOptions = {
				isShow: true,
				...message
			};
			Vue.$store.commit("updateGroupInviteNotifications", groupInviteOptions);
			break;
		case "joinGroupNotifications": // 
			let groupOptions = {
				isShow: true,
				...message
			};
			Vue.$store.commit("updateGroupNotifications", groupOptions);
			break;
		case "memberJoinPublicGroupSuccess": // 
			Vue.$store.dispatch("onGetGroupinfo", { select_id });
			Message({
				type: "success",
				message: `${message.from}`
			});
			break;
		case "joinPublicGroupSuccess":  // 
			Vue.$store.dispatch("onGetGroupUserList");
			break;
		case "deleteGroupChat": // 
			Vue.$store.dispatch("onGetGroupUserList");
			Message({
				type: "error",
				message: `${message.from}${message.gid} `
			});
			break;
		case "removedFromGroup": // 
			Vue.$store.dispatch("onGetGroupUserList");
			Message({
				type: "success",
				message: "" + message.from + "" + message.gid
			});
			break;
		case "leaveGroup":
			Vue.$store.dispatch("onGetGroupinfo", { select_id });
		default:
			break;
		}
	}, // -
	onRoster: function(message){
		console.log("onRoster", message);
	}, // 
	onInviteMessage: function(message){
		console.log("onInviteMessage", message);
	}, // 
	onOnline: function(){
		console.log("onOnline ");
	}, // 
	onOffline: function(){
		console.log("onOffline ");
	}, // 
	onError: function(message){
		console.log("onError", message);
		if(message.type == 28){
			console.log("");
		}
		else if(JSON.parse(_.get(message, "data.data.error_description")) == "user not found"){
			Message.error("");
		}
		else if(JSON.parse(_.get(message, "data.data.error_description")) == "invalid password"){
			Message.error("");
		}
		else if(JSON.parse(_.get(message, "data.data.error_description")) == "user not activated"){
			Message.error("");
		}
		else if(message.type == "504"){
			Message("");
		}
		// 
		// Vue.$router.push({ path: '/login' });
	}, // 
	onRecallMessage: message => {
		console.log("", message);
		message.status = "recall";
		message.msg = "";
		Vue.$store.commit("updateMessageStatus", message);
	},
	onBlacklistUpdate: function(list){ // 
		// list
		// 
		console.log("", list);
		let blackList = list;
		Vue.$store.commit("updateBlackList", blackList);
	},
	onReceivedMessage: function(message){
		console.log("onReceivedMessage", message);
		Vue.$store.commit("updateMessageMid", message);
		message.status = "sent";
		Vue.$store.commit("updateMessageStatus", message);
	}, // 

	onDeliveredMessage: function(message){
		console.log("onDeliveredMessage", message);
		// Vue.$store.commit('updateMessageStatus', message)
	}, // 

	onReadMessage: function(message){
		console.log("onReadMessage", message);
		message.status = "read";
		Vue.$store.commit("updateMessageStatus", message);
	}, // 

	onCreateGroup: function(message){
		console.log("onCreateGroup", message);
		Vue.$store.dispatch("onGetGroupUserList");
	}, // createGroupNew
	onMutedMessage: function(message){
		console.log("onMutedMessage", message);
	} // AA
});

WebIM.WebRTC = webrtc;
WebIM.EMedia = emedia;
export default WebIM;



